@import "../../../../styles/utility/index";

.#{$p-global-class-prefix}headline {
  $bem-block: &;
  color: inherit;

  &--inline {
    display: inline;
  }

  &--align {
    &-left {
      text-align: left;
    }

    &-center {
      text-align: center;
    }

    &-right {
      text-align: right;
    }
  }

  &--wrap {
    overflow-wrap: break-word;
    word-wrap: break-word;
  }

  &--ellipsis {
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  // headlines
  &--large-title {
    @include p-large-title;
  }

  &--headline-1 {
    @include p-headline-1;
  }

  &--headline-2 {
    @include p-headline-2;
  }

  &--headline-3 {
    @include p-headline-3;
  }

  &--headline-4 {
    @include p-headline-4;
  }

  &--headline-5 {
    @include p-headline-5;
  }

  &--headline-6 {
    @include p-headline-5;
  }

  &--color {
    &-light {
      color: $p-color-porsche-light;
    }

    &-black {
      color: $p-color-porsche-black;
      &#{$bem-block}--theme-dark {
        color: $p-color-porsche-light;
      }
    }
  }

  &--theme-dark {
    color: $p-color-porsche-light;
  }
}
